var _this;
export default {
	data() {
		return {
			list:[
				
			]
		}
	},
	mounted() {
		_this = this;
	},
	onLoad(){
		uni.request({
			url: this.serverUrl + "tb/tbtopic/list",
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				_this.list = res.data.list;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		toPost(index){
			let topic = this.list[index];
			console.log(topic);
			uni.navigateTo({
				url:"/pages/topic/post?id=" + topic.id + "&title=" +  topic.title
			})
		}
	}
}
